<style lang="less">
  @import '../../styles/common.less';
  @import '../../styles/table.less';
</style>
<template>
  <div>
    <Row>
      <Col span="7">
      <Card :dis-hover="true" class="show-shadow">
        <p slot="title">
          <Icon type="md-refresh"/>
          添加git仓库
        </p>
        <div class="edittable-testauto-con">
          <Form ref="formValidate" :model="formItem" :rules="ruleInline">
            <FormItem label="项目名称:" prop="app_name">
              <Input v-model="formItem.app_name" placeholder="请输入"></Input>
            </FormItem>
            <FormItem label="地址:" prop="repo_url">
              <Input v-model="formItem.repo_url" placeholder="请输入"></Input>
            </FormItem>
            <FormItem label="是否免密:" prop="is_auth">
              <Select v-model="formItem.is_auth" placement="bottom-start" placeholder="选择">
                <Option v-for="item in connectType" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
            </FormItem>
            <FormItem label="用户名:" prop="username">
              <Input v-model="formItem.username" placeholder="请输入" :disabled="formItem.is_auth == '0'"></Input>
            </FormItem>
            <FormItem label="密码:" prop="password">
              <Input v-model="formItem.password" placeholder="请输入" :disabled="formItem.is_auth == '0'" type="password"></Input>
            </FormItem>
            <Button type="success" @click="addConnection()" style="margin-left: 5%">确定</Button>
            <Button type="warning" @click="resetForm()" style="margin-left: 5%">重置</Button>
          </Form>
        </div>
      </Card>
      </Col>
      <Col span="17" class="padding-left-10">
      <Card :dis-hover="true" class="show-shadow">
        <p slot="title">
          <Icon type="md-apps"/>
          Git仓库连接信息表
        </p>
        <Input v-model="query.app_name" placeholder="请填写连接名" style="width: 15%" clearable></Input>
        <Button @click="queryData" type="primary" class="margin-left-10">查询</Button>
        <Button @click="queryCancel" type="warning" class="margin-left-10">重置</Button>
        <div class="edittable-con-1">
          <Table :columns="columns" :data="tableData" :loading="tableLoading">
            <template slot-scope="{ row }" slot="action">
              <Button type="primary" size="small" @click="viewConnectionModal(row)" style="margin-right: 5px">详情</Button>
            </template>
          </Table>
        </div>
        <br>
        <Page :total="totalNumber" show-elevator @on-change="pageChange" :page-size="page_size" ref="page"></Page>
      </Card>
      </Col>
    </Row>

    <Modal v-model="baseinfo" :width="500" okText="保存" @on-ok="modifyBase">
      <h3 slot="header" style="color:#2D8CF0">Git连接信息</h3>
      <Form :label-width="100" label-position="right">
        <FormItem label="项目名称:">
          <Input v-model="editbaseinfo.app_name" readonly></Input>
        </FormItem>
        <FormItem label="连接地址:">
          <Input v-model="editbaseinfo.repo_url"></Input>
        </FormItem>
        <FormItem label="是否免密:" prop="is_auth">
          <Select v-model="editbaseinfo.is_auth" placement="bottom-start" placeholder="选择">
            <Option v-for="item in connectType" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </FormItem>
        <FormItem label="用户名:">
          <Input v-model="editbaseinfo.username"  :disabled="editbaseinfo.is_auth == '0'"></Input>
        </FormItem>
        <FormItem label="密码:">
          <Input v-model="editbaseinfo.password" type="password"  :disabled="editbaseinfo.is_auth == '0'"></Input>
        </FormItem>
      </Form>
    </Modal>

  </div>
</template>
<script>
  import '../../styles/tablesmargintop.css'
  import { getGitList, addGit, updateGit, deleteGit } from '@/api/settings'

  export default {

    name: 'GitSetting',
    data() {
      return {
        connectType: [
          {value: '0', label: '是'},
          {value: '1', label: '否'}
        ],
        tableData: [],
        columns: [
          {
            title: '项目名称',
            key: 'app_name',
            width:150
          },
          {
            title: '地址',
            key: 'repo_url'
          },
          {
            title: '操作',
            key: 'action',
            slot: 'action',
            width:100
          }
        ],
        // 添加数据库信息
        formItem: {
          app_name: '',
          repo_url: '',
          username: '',
          password: '',
          is_auth: ''
        },
        // 添加表单验证规则
        ruleInline: {
          app_name: [{
            required: true,
            message: '请填写项目名',
            trigger: 'blur',
          }],
          repo_url: [{
            required: true,
            message: '请填写repo地址',
            trigger: 'blur'
          }],
          is_auth: [
            {
              required: true,
              message: '请选择',
              trigger: 'blur',
            }
          ],
        },
        totalNumber: 0,
        currentPage: 1,
        page_size: 10,
        tableLoading: false,
        baseinfo: false,
        editbaseinfo: {},
        query: {
          app_name: '',
        },
        testLoading: false,
        addLoading: false
      }
    },
    methods: {
      resetForm() {
        this.formItem = this.$config.clearObj(this.formItem)
      },
      addConnection() {
        this.$refs['formValidate'].validate((valid) => {
          if (valid) {
            this.$Modal.confirm({
              title: '请确认是否保存代码仓库连接信息',
              'z-index': 1010,
              // content: '请确认创建Review任务',
              onOk: () => {
                this.addLoading = true
                addGit(this.formItem).then(res => {
                  if (res.data.status === 'success') {
                    this.$Message.success({
                      content: '代码仓库连接信息已经保存',
                      duration: 5,
                      closable: true
                    });
                    this.$refs['formValidate'].resetFields()
                  } else {
                    this.$Message.error({
                      content: '保存连接信息错误',
                      duration: 5,
                      closable: true
                    });
                  }
                }).catch(e => {
                  this.bus.$emit('errorHandler', e);
                }).finally(() => {
                  this.addLoading = false
                  this.getData()
                })
              },
              onCancel: () => {
                // this.$Message.info('Clicked cancel');
              }
            });
          }
        })
      },
      viewConnectionModal(row) {
        this.baseinfo = true;
        this.editbaseinfo = row
      },
      getData() {
        this.tableLoading = true;
        const limit = this.page_size
        const offset = this.page_size * (this.currentPage - 1)
        getGitList(limit, offset, this.query.app_name).then(res => {
          console.log(res)
          this.tableData = res.data.data
          this.totalNumber = res.data.count
        }).catch(e => {
          this.bus.$emit('errorHandler', e);
        }).finally(() => {
          this.tableLoading = false;
        })

      },
      modifyBase() {
        this.baseinfo = false;
        updateGit(this.editbaseinfo).then(res => {
          if (res.data.status === 'success') {
            this.$Message.success({
              content: '数据连接信息已经保存',
              duration: 5,
              closable: true
            });
          } else {
            this.$Message.error({
              content: '保存信息错误',
              duration: 5,
              closable: true
            });
          }
        }).catch(e => {
          this.bus.$emit('errorHandler', e);
        }).finally(() => {
          this.getData()
        })
      },
      queryData() {
        this.getData()
      },
      queryCancel() {
        this.$config.clearObj(this.query);
        this.getData()
      },
      pageChange (p) {
        this.currentPage = p
        this.getData()
      }
    },
    mounted() {
      this.getData()
    }
  }
</script>
