<style lang="less">
  @import '../../styles/common.less';
  @import '../../styles/table.less';
</style>
<template>
  <div>
    <Row>
      <Col span="7" style="padding: 0 0 3px 3px;">
      <Card :dis-hover="true" class="show-shadow">
        <p slot="title">
          <Icon type="md-refresh"/>
          添加数据库实例
        </p>
        <div class="edittable-testauto-con">
          <Form ref="formValidate" :model="formItem" :rules="ruleInline">
            <FormItem label="连接名称:" prop="description">
              <Input v-model="formItem.description" placeholder="请输入"></Input>
            </FormItem>
            <FormItem label="连接类型:" prop="db_type">
              <Select v-model="formItem.db_type" placement="bottom-start" placeholder="选择类型">
                <Option v-for="item in connectType" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
            </FormItem>
            <FormItem label="数据库地址:" prop="host">
              <Input v-model="formItem.host" placeholder="请输入"></Input>
            </FormItem>
            <FormItem label="端口:" prop="port">
              <Input v-model="formItem.port" placeholder="请输入"></Input>
            </FormItem>
            <FormItem label="用户名:" prop="username">
              <Input v-model="formItem.username" placeholder="请输入"></Input>
            </FormItem>
            <FormItem label="实例名:" prop="instance_name">
              <Input v-model="formItem.instance_name" placeholder="请输入"></Input>
            </FormItem>
            <FormItem label="密码:" prop="passwd">
              <Input v-model="formItem.passwd" placeholder="请输入" type="password"></Input>
            </FormItem>
            <Button type="primary" :loading="testLoading" @click="testConnection()">测试连接</Button>
            <Button type="success" @click="addConnection()" :loading="addLoading" style="margin-left: 5px">确定</Button>
            <Button type="warning" @click="resetForm()" style="margin-left: 5px">重置</Button>
          </Form>
        </div>
      </Card>
      </Col>
      <Col span="17" class="padding-left-10">
      <Card :dis-hover="true" class="show-shadow">
        <p slot="title">
          <Icon type="md-apps"/>
          数据库实例详情表
        </p>
        <Input v-model="query.description" placeholder="请填写连接名" style="width: 15%" clearable></Input>
        <Button @click="queryData" type="primary" class="margin-left-10">查询</Button>
        <Button @click="queryCancel" type="warning" class="margin-left-10">重置</Button>
        <div class="edittable-con-1">
          <Table :columns="columns" :data="tableData" :loading="tableLoading">
            <template slot-scope="{ row }" slot="action">
              <Button type="primary" size="small" @click="viewConnectionModal(row)" style="margin-right: 5px">详情</Button>
              <Button type="error" size="small" @click="delConnection(row)">删除</Button>
            </template>
          </Table>
        </div>
        <br>
        <Page :total="totalNumber" show-elevator @on-change="pageChange" :page-size="page_size" ref="i-page"></Page>
      </Card>
      </Col>
    </Row>

    <Modal v-model="baseinfo" :width="500" okText="保存" @on-ok="modifyBase">
      <h3 slot="header" style="color:#2D8CF0">数据库连接信息</h3>
      <Form :label-width="100" label-position="right">
        <FormItem label="连接名称:">
          <Input v-model="editbaseinfo.description" readonly></Input>
        </FormItem>
        <FormItem label="数据库地址:">
          <Input v-model="editbaseinfo.host"></Input>
        </FormItem>
        <FormItem label="端口:">
          <Input v-model="editbaseinfo.port"></Input>
        </FormItem>
        <FormItem label="用户名:">
          <Input v-model="editbaseinfo.username"></Input>
        </FormItem>
        <FormItem label="密码:">
          <Input v-model="editbaseinfo.passwd" type="password"></Input>
        </FormItem>
      </Form>
    </Modal>

  </div>
</template>
<script>
  import '../../styles/tablesmargintop.css'
  import { getSchemaProfileList, addSchemaProfile, deleteSchemaProfile, updateSchemaProfile, testSchemaProfile } from '@/api/settings'

  export default {

    name: 'DBSetting',
    data() {
      return {
        connectType: [
          {value: 'MYSQL', label: 'Mysql'},
          {value: 'ORACLE', label: 'Oracle'}
        ],
        tableData: [],
        columns: [
          {
            title: '连接名称',
            key: 'description'
          },
          {
            title: '类型',
            key: 'db_type',
          },
          {
            title: '数据库地址',
            key: 'host'
          },
          {
            title: '用户名',
            key: 'username'
          },
          {
            title: '操作',
            key: 'action',
            slot: 'action'
          }
        ],
        // 添加数据库信息
        formItem: {
          description: '',
          host: '',
          username: '',
          instance_name: '',
          passwd: '',
          port: '',
          db_type: 'oracle',
          profile_name: 'test',
          tenant: sessionStorage.getItem('tenant_name')
        },
        // 添加表单验证规则
        ruleInline: {
          description: [
            {
              required: true,
              message: '请填写连接名称',
              trigger: 'blur',
            }
          ],
          host: [{
            required: true,
            message: '请填写连接地址',
            trigger: 'blur'
          }],
          db_type: [{
            required: true,
            message: '请选择连接类型',
            trigger: 'blur'
          }],
          instance_name: [{
            required: true,
            message: '请填写实例名',
            trigger: 'blur'
          }],
          username: [{
            required: true,
            message: '请填写用户名',
            trigger: 'blur'
          }],
          port: [{
            required: true,
            message: '请填写端口',
            trigger: 'blur'
          }],
          passwd: [{
            required: true,
            message: '请填写密码',
            trigger: 'blur'
          }]
        },
        comList: [],
        totalNumber: 0,
        currentPage: 1,
        page_size: 10,
        tableLoading: false,
        baseinfo: false,
        editbaseinfo: {},
        query: {
          description: '',
        },
        testLoading: false,
        addLoading: false
      }
    },
    methods: {
      resetForm() {
        this.formItem = this.$config.clearObj(this.formItem)
      },
      testConnection() {
        this.$refs['formValidate'].validate((valid) => {
          if (valid) {
            this.testLoading = true
            this.formItem.profile_name = this.formItem.description
            testSchemaProfile(this.formItem).then(res => {
              if (res.data.status === 'success') {
                this.$Message.success({
                  content: '数据库连通测试已经通过，请添加相应信息',
                  duration: 5,
                  closable: true
                });
              } else {
                this.$Message.error({
                  content: '无法连接数据库或没有表信息，请确认配置信息',
                  duration: 5,
                  closable: true
                });
              }
            }).catch(e => {
              this.bus.$emit('errorHandler', e);
            }).finally(() => {
              this.testLoading = false
              this.getData()
            })
          } else {
            this.$Message.error({
              content: '请将信息填写完整！',
              duration: 5,
              closable: true
            });
          }
        })
      },
      addConnection() {
        this.$refs['formValidate'].validate((valid) => {
          if (valid) {
            this.$Modal.confirm({
              title: '请先测试数据库是否连通！，确认是否保存数据库连接信息！？',
              'z-index': 1010,
              // content: '请确认创建Review任务',
              onOk: () => {
                this.addLoading = true
                addSchemaProfile(this.formItem).then(res => {
                  if (res.data.status === 'success') {
                    this.$Message.success({
                      content: '数据连接信息已经保存',
                      duration: 5,
                      closable: true
                    });
                    this.$refs['formValidate'].resetFields()
                  } else {
                    this.$Message.error({
                      content: '保存信息错误',
                      duration: 5,
                      closable: true
                    });
                  }
                }).catch(e => {
                  this.bus.$emit('errorHandler', e);
                }).finally(() => {
                  this.addLoading = false
                  this.getData()
                })
              },
              onCancel: () => {
                // this.$Message.info('Clicked cancel');
              }
            });
          }
        })
      },
      viewConnectionModal(row) {
        this.baseinfo = true;
        this.editbaseinfo = row
      },
      delConnection(row) {

        this.$Modal.confirm({
          title: `请确认是否要删除名为：${row.description} 的连接信息吗!?`,
          'z-index': 1010,
          // content: '请确认创建Review任务',
          onOk: () => {
            this.tableLoading = true
            this.addLoading = true
            deleteSchemaProfile(row.id).then(res => {
              if (res.data.status === 'success') {
                this.$Message.success({
                  content: '数据连接信息已经保存',
                  duration: 5,
                  closable: true
                });
              } else {
                this.$Message.success({
                  content: '删除信息失败',
                  duration: 5,
                  closable: true
                });
              }
            }).catch(e => {
              this.bus.$emit('errorHandler', e);
            }).finally(() => {
              this.tableLoading = false
              this.getData()
            })
          },
          onCancel: () => {
            // this.$Message.info('Clicked cancel');
          }
        });
      },
      getData() {
        this.tableLoading = true;
        const limit = this.page_size
        const offset = this.page_size * (this.currentPage - 1)
        getSchemaProfileList(limit, offset, this.query.description).then(res => {
          console.log(res)
          this.tableData = res.data.data
          this.totalNumber = res.data.count
        }).catch(e => {
          this.bus.$emit('errorHandler', e);
        }).finally(() => {
          this.tableLoading = false;
        })
      },
      modifyBase() {
        this.baseinfo = false;
        updateSchemaProfile(this.editbaseinfo).then(res => {
          if (res.data.status === 'success') {
            this.$Message.success({
              content: '数据连接信息已经保存',
              duration: 5,
              closable: true
            });
          } else {
            this.$Message.error({
              content: '保存信息错误',
              duration: 5,
              closable: true
            });
          }
        }).catch(e => {
          this.bus.$emit('errorHandler', e);
        }).finally(() => {
          this.getData()
        })
      },
      queryData() {
        this.getData()
      },
      queryCancel() {
        this.$config.clearObj(this.query);
        this.getData()
      },
      pageChange (p) {
        this.currentPage = p
        this.getData()
      }
    },
    mounted() {
      this.getData()
    }
  }
</script>
